import { FontStyle, GlobalStyle } from "./components/GlobalStyles";
import Theme from "./components/Theme/Theme";
import { TokenProvider } from "./contexts/TokenProvider";
import { UserProvider } from "./contexts/UserProvider";
import AppRouter from "./router/AppRouter";




function App() {

  return (
    <>
      <TokenProvider>
        <UserProvider>
          <Theme>
              <FontStyle/>
              <GlobalStyle/>
                  <AppRouter/>
          </Theme>
        </UserProvider>
      </TokenProvider>
    </>
  );
}

export default App;
