import React from 'react';
import { IoCarSportOutline, IoChatbubblesOutline, IoConstructOutline, IoSpeedometerOutline, IoStorefrontOutline } from 'react-icons/io5';
import styled from 'styled-components';
import Tooltip from '../atoms/icons/Tooltip';
import { BorderRadius, Devices, ZIndex } from '../GlobalStyles';
import {NavLink} from 'react-router-dom'

const SidebarContainer = styled.div`
    height:10vh ;
    width: 100%;
    display: flex;
    flex-direction:row;
    align-items:center;
    justify-content:space-evenly;
    background-color:${({theme})=> theme.bg.primary};
    position:fixed;
    bottom:0;
    left:0;
    border-top-right-radius:${BorderRadius.lg};
    border-top-left-radius:${BorderRadius.lg};
    transition: all 0.25s ease;
    z-index:${ZIndex[50]};
    @media ${Devices.Laptops}{
        border-top-left-radius:0px;
        border-bottom-right-radius:${BorderRadius.lg};
        border-top-right-radius:${BorderRadius.lg};
        height:100vh ;
        width: 12vh;
        flex-direction:column;
        align-items:center;
        top:0;
        left:0;
    }
`

const Sidebar = () => {
  return (
        <SidebarContainer>
            <NavLink to='/' 
            style={({isActive})=> isActive ? { background:'#4B59F7',borderRadius:BorderRadius.lg} : undefined}>
                <Tooltip color='fontLight' size='lg' text='Inicio'>
                    <IoSpeedometerOutline/>
                </Tooltip>
            </NavLink>
            {/* ventas */}
            <NavLink to='/ventas' 
            style={({isActive})=> isActive ? { background:'#4B59F7',borderRadius:BorderRadius.lg} : undefined}>
                <Tooltip color='fontLight' size='lg' text='Ventas'>
                    <IoStorefrontOutline/>
                </Tooltip>
            </NavLink>
            <NavLink to='/productos' 
            style={({isActive})=> isActive ? { background:'#4B59F7',borderRadius:BorderRadius.lg} : undefined}>
                <Tooltip color='fontLight' size='lg' text='Filtros'>
                    <IoConstructOutline/>
                </Tooltip>
            </NavLink>
            <NavLink to='/clientes' 
            style={({isActive})=> isActive ? { background:'#4B59F7',borderRadius:BorderRadius.lg} : undefined}>
                <Tooltip color='fontLight' size='lg' text='Clientes'>
                    <IoCarSportOutline/>
                </Tooltip>
            </NavLink>
            <NavLink to='/mensajeria' 
            style={({isActive})=> isActive ? { background:'#4B59F7',borderRadius:BorderRadius.lg} : undefined}>
                <Tooltip color='fontLight' size='lg' text='Mensajes'>
                    <IoChatbubblesOutline/>
                </Tooltip>
            </NavLink>
        </SidebarContainer>
  );
};

export default Sidebar;
