import React from 'react'
import styled from 'styled-components'
import { BorderRadius, ZIndex } from '../../GlobalStyles'
import IconContainer from './IconContainer'
import PropTypes from 'prop-types'

    const TooltipContainer = styled.div`
        position:relative ;
        cursor: pointer;
        padding: 0.5rem;
        `
    const TooltipText = styled.span`
        cursor: pointer;
        position: absolute;
        background-color:${({theme})=>theme.fontColor.black} ;
        color:${({theme})=>theme.fontColor.primary} ;
        top:${({top})=> top};
        padding: 0.25rem 0.75rem;
        border-radius:${BorderRadius.md};
        opacity: 0;
        transition: opacity 0.5s ease-in-out;
        z-index:${ZIndex[10]};
        &:hover{ 
            opacity: 1;
        }
    `

const Tooltip = ({children,text,color,size,top='-20%'}) => {
    return (
        <TooltipContainer>
            {text ?
            <TooltipText top={top}>
                {text}
            </TooltipText>
            :
            ''
            }
            <IconContainer color={color} size={size}>
                {children}
            </IconContainer>
        </TooltipContainer>
    )
}
Tooltip.propTypes ={
    text:PropTypes.string,
    top:PropTypes.string,
    size:PropTypes.oneOf(['sm','md','lg','xl']),
    color:PropTypes.oneOf(['primary','secondary','bgPrimary','bgSecondary','fontLight','fontDark','success','warning','danger']),
}

export default Tooltip