import React, { useState } from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';
import Message from '../../molecules/texts/Message';
import OutletSearchBar from '../../organisms/OutletSearchBar';
import Table, { Col, TableRow } from '../../organisms/Table';
import NewVehicle from '../../organisms/NewVehicle'
import { Devices } from '../../GlobalStyles';

const Container = styled.div`
    margin: 2rem 0;
`
const ResponsiveTable = styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    margin:1.5rem 0;
    max-width:1500px;
    overflow:hidden;
    overflow-x:scroll ;
    overflow-y:scroll ;
    @media ${Devices.Tablets}{
        overflow-x:auto ;
        overflow-y:auto ;
    }
`

const header = ['Patente','Marca','Modelo','Año','Km','']

const VehiculosTemplate = ({
    form,
    handleForm,
    setForm,
    search,
    handleSearch,
    isLoading,
    vehicles,
    handleDelete,
    message,
    options,
    handleNew,
}) => {
    const [addOne, setAddOne] = useState(false)
    const navigate = useNavigate();
  return (
        <Container>
            <OutletSearchBar
                placeholder='Ingrese una patente'
                search={search}
                onChange={handleSearch}
                addOne={addOne}
                setAddOne={setAddOne}
            />
            <NewVehicle
            form={form}
            setForm={setForm}
            onChange={handleForm} 
            open={addOne} 
            setOpen={setAddOne}
            options={options}
            onSubmit={handleNew}
            message={message}
            />
            {
                message &&(
                        <Message color='danger' light={true} width='3/4'>
                            {message.msg}
                        </Message>
                )
            }
            <ResponsiveTable>
                    {
                        isLoading ?
                        <h2>Cargando</h2>
                        :
                        vehicles && vehicles.length > 0 &&(
                            <Table header={header}>{
                                vehicles.map(el=>{
                                    return(
                                        <TableRow key={el._id}
                                        seeMore={()=> navigate(`/clientes/${el.owner._id}`)}
                                        deleteOne={()=>handleDelete(el._id,el.patent)}
                                        >
                                            <Col>{el.patent}</Col>
                                            <Col>{el.mark}</Col>
                                            <Col>{el.model}</Col>
                                            <Col>{el.year}</Col>
                                            <Col>{el.km}</Col>
                                        </TableRow>
                                        )})
                                        }
                            </Table>
                        )

                    }    
            </ResponsiveTable>
        </Container>
  );
};

export default VehiculosTemplate;
