import React from 'react';
import styled from 'styled-components'
import ActionButton from '../atoms/buttons/ActionButton';
import Stack from '../atoms/divs/Stack';
import Input from '../atoms/inputs/Input';
import { BorderRadius } from '../GlobalStyles';
import Dropdown, { DropdownItem } from '../molecules/buttons/Dropdown';
import { colorsFunction } from '../Theme/Theme';
import PropTypes from 'prop-types'

const Box = styled.div`
    border-radius:${BorderRadius.lg} ;
    &:hover{
        box-shadow:1px 1px 5px ${({theme})=> colorsFunction('bgSecondary',theme)};
    }
`

const OutletSearchBar = ({
    dropdown=false,
    selected,
    addOne,setAddOne,
    addMany,setAddMany,
    editMany,setEditMany,
    search,onChange,
    deleteMany,
    placeholder,
}) => {

  return (
          <Stack spacing={4} wrap='true'>
              {
                  dropdown &&(
                    <Box>
                    <Dropdown mobileTop='27%' top='22%' mobileLeft='20%'>
                        <DropdownItem onClick={()=>setAddMany(!addMany)}>
                            Crear Varios
                        </DropdownItem>
                        <DropdownItem onClick={()=>setEditMany(!editMany)}>
                            Editar Varios
                        </DropdownItem>
                    </Dropdown>
                    </Box>
                  )
              }
            <Input
                type='search'
                name='search'
                value={search}
                placeholder={placeholder}
                color='bgSecondary'
                onChange={onChange}
                width='2/5'
            />
            <ActionButton onClick={()=>setAddOne(!addOne)}>
                Nuevo
            </ActionButton>
            {
                selected &&(
                    <ActionButton type='danger' onClick={deleteMany}>
                        Borrar
                    </ActionButton>
                )
            }
          </Stack>
  );
};

OutletSearchBar.propTypes={
    dropdown:PropTypes.bool,
    selected:PropTypes.bool,
    addOne:PropTypes.bool,
    setDddOne:PropTypes.func,
    addMany:PropTypes.bool,
    setAddMany:PropTypes.func,
    editMany:PropTypes.bool,
    setEditMany:PropTypes.func,
    search:PropTypes.string,
    onChange:PropTypes.func,
    placeholder:PropTypes.string,
}

export default OutletSearchBar;
