import React from 'react';
import Select from 'react-select'
import styled from 'styled-components';
import { widthFunction } from '../../GlobalStyles';


const Container = styled.div`
  display:flex;
  flex-direction:column;
  align-items:center;
  /* justify-content:center; */
  width: ${({ width }) => widthFunction(width)};
`

const SelectSearch = ({
  options=[
    {
      value:'kljsdhfkl', label:'Ivan',
    },
    {value:'kljsdhfkl', label:'Juan'},
    {value:'kljsdhfkl', label:'Pedro'},
  ],
  styles,
  placeholder,
  onChange,
  value,
  width='100%',
}) => {



  return (
    <Container width={width}>

    <Select 
      value={value}
      options={options}
      styles={styles}
      placeholder={placeholder}
      onChange={onChange}
      required
      />
    </Container>
  );
};

export default SelectSearch;
