import React from 'react';
import { Outlet } from 'react-router-dom';
import InternBar from '../../organisms/InternBar';

const pages =[
    {
        name:'Clientes',
        page:'/clientes'
    },
    {
        name:'Vehiculos',
        page:'/clientes/vehiculos'
    }
]

const ClientsTemplate = () => {
  return (
      <>
        <InternBar items={pages}/>
        <Outlet/>
      </>
  );
};

export default ClientsTemplate;
