import { IoArrowBack } from "react-icons/io5";
import styled from "styled-components";
import Stack from "../../atoms/divs/Stack";
import IconContainer from "../../atoms/icons/IconContainer";
import Heading from "../../atoms/texts/Heading";



const Container = styled.div`
  margin: 2rem;
  display: flex;
  flex-direction:column;
  gap:2rem;
`
const CustomButton = styled.button`
  background-color:transparent;
  border: none;
`

const ClientProfileTemplate = ({client}) => {

  
  return (
     <Container>
       <Stack content="start" spacing={10}>
         <CustomButton>
            <IconContainer size="lg">
                <IoArrowBack/>
            </IconContainer>
         </CustomButton>
         <Heading color="bgPrimary" weight="bold">
            Informacion del Cliente
         </Heading>
       </Stack>

         {
           client &&(
        <Stack direction="column" items="start">
             <Heading>nombre:  {client.name}</Heading>
             <Heading>apellido:  {client.surname}</Heading>
             <Heading>telefono:  {client.phone}</Heading>
             <Heading>email:  {client.email}</Heading>
             <Heading>vehiculos:  {client.vehicles.length}</Heading>
       </Stack>
           )
         }
     </Container>
  )
};

export default ClientProfileTemplate;
