import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types'
import { colorsFunction } from '../../Theme/Theme';
import { FontWeight } from '../../GlobalStyles';

const Text = styled.p`
    color:${({color,theme})=> colorsFunction(color,theme)};
    font-weight:${({weight})=>{
      switch(weight){
        case 'semibold':
          return weight = FontWeight.semibold;
        case 'bold':
          return weight = FontWeight.bold;
        case 'extrabold':
          return weight = FontWeight.extrabold;
        default :
        return weight = FontWeight.normal;
      }
    }}; 

`

const Paragraph = ({children, color , weight='normal'}) => {
  return <Text color={color} weight={weight}>{children}</Text>
};

Paragraph.propTypes ={
    children:PropTypes.string,
    color:PropTypes.oneOf(['primary','secondary','bgPrimary','bgSecondary','fontLight','fontDark','success','warning','danger']),
    weight:PropTypes.oneOf(['normal','semibold','bold','extrabold'])
}

export default Paragraph;
