import React, { useEffect, useState } from 'react';
import VehiculosTemplate from '../../components/templates/clients/VehiculosTemplate';
import AxiosInstance from '../../utils/AxiosInstance';

const initialForm={
    patent:'',
    owner:'',
    mark:'',
    model:'',
    year:'',
    km:'',
}

const Vehicles = () => {
    const [form, setForm] = useState(initialForm)
    const [search, setSearch] = useState({search:''})
    const [isLoading, setIsLoading] = useState(true)
    const [vehicles, setVehicles] = useState([])
    const [options, setOptions] = useState([])
    const [message, setMessage] = useState(null)
    const [deleted, setDeleted] = useState(null)
    const [newVehicle, setNewVehicle] = useState(null)

// Efectos
    useEffect(()=>{
        async function getVehicles(){
            let {data} = await AxiosInstance.get('vehicles')
            if(data && data.response.vehicles.length > 0){
                setIsLoading(false)
                setVehicles(data.response.vehicles);
            }
        }

        async function searchVehicle(){
            setIsLoading(true)
            if(search && search.search.length > 2){
                let patent = search.search;
                let {data} = await  AxiosInstance.get(`vehicles/${patent}`);
                if(data && data.response.vehicles.length > 0){
                    setVehicles(data.response.vehicles)
                }
            }
        }

        if(search && search.search.length > 2){
            searchVehicle()
            setIsLoading(false)
        }else{
            getVehicles()
        }
        
    },[search,deleted,newVehicle])

    useEffect(()=>{
        
        async function getClients(){
            let {data} = await AxiosInstance.get('clients');
            if(data && data.response.clients.length > 0){
                let clients = data.response.clients;
                clients = clients.map(el=>{
                    return({
                        value:el._id,
                        label:`${el.name} ${el.surname}`,
                    })
                })
                setOptions(clients)
            }
        }
        getClients()
    },[])


// Funciones
    function handleForm(e){
        setForm({
            ...form,
            [e.target.name]:e.target.value
        })
    }

    function handleSearch(e){
        setSearch({
            [e.target.name]:e.target.value
        })
    }

    async function handleDelete(vehicleId,patent){
        alert(`Al eliminar el vehiculo ${patent} se perderan todos sus datos almacenados`);
        let res = window.confirm(`Estas seguro de eliminar el vehiculo ${patent}`)
        if(res){
            let {data} = await AxiosInstance.delete(`vehicle/${vehicleId}`)
            if(data){
                setMessage(data)
                setDeleted(data)
                setInterval(()=>{
                    setMessage(null)
                },5000)
            }
        }
    }

    async function handleNew(e){
        e.preventDefault();
        let string = new URLSearchParams();
        string.append(`patent`,`${form.patent}`)
        string.append(`owner`,`${form.owner}`)
        string.append(`mark`,`${form.mark}`)
        string.append(`model`,`${form.model}`)
        string.append(`year`,`${form.year}`)
        string.append(`km`,`${form.km}`)
        let {data} = await AxiosInstance.post('vehicle',string);
        if(data){
            setNewVehicle(data)
            setMessage(data)
            setInterval(()=>{
                setMessage(null)
            },5000)
        }

        setForm(initialForm)
    }

  return (
      <VehiculosTemplate
        form={form}
        handleForm={handleForm}
        setForm={setForm}
        search={search.search}
        handleSearch={handleSearch}
        isLoading={isLoading}
        vehicles={vehicles}
        message={message}
        handleDelete={handleDelete}
        options={options}
        handleNew={handleNew}

      />
  );
};

export default Vehicles;
