import React, { useState } from 'react';
import styled from 'styled-components';
import { Devices } from '../../GlobalStyles';
import Message from '../../molecules/texts/Message';
import NewProduct from '../../organisms/NewProduct';
import OutletSearchBar from '../../organisms/OutletSearchBar';
import Table, { Col, TableRow } from '../../organisms/Table';

const Container = styled.div`
    margin: 2rem 0;

`
const ResponsiveTable = styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    margin:1.5rem 0;
    /* max-width:500px; */
    overflow:hidden;
    overflow-x:scroll ;
    overflow-y:scroll ;
    @media ${Devices.Tablets}{
        overflow-x:auto ;
        overflow-y:auto ;
    }
`

const header = ['Codigo','Precio','Cantidad','Categoria','Aplicacion',' ']

const FiltrosTemplate = ({

    form,
    handleForm,
    setForm,
    search,
    handleSearch,
    isLoading,
    options,
    filtros,
    message,
    handleDelete,
    handleNew

}) => {
  const [addOne, setAddOne] = useState(false)
  const [addMany, setAddMany] = useState(false)
  const [editMany, setEditMany] = useState(false)
return (
      <Container>
          <OutletSearchBar
              dropdown={true}
              placeholder='Ingrese el codigo del producto'
              search={search} onChange={handleSearch}
              addOne={addOne} setAddOne={setAddOne}
              addMany={addMany} setAddMany={setAddMany}
              editMany={editMany} setEditMany={setEditMany}
          />
          <NewProduct
          open={addOne}
          setOpen={setAddOne}
          onSubmit={handleNew}
          form={form}
          onChange={handleForm}
          setForm={setForm}
          options={options}
          message={message}
          />

                {
                    message &&(
                        <Message color='danger' light width='3/4'>
                                {message.msg}
                        </Message>
                    )
                }


                <ResponsiveTable>
                    {
                        isLoading ?
                        <h2>Carganddo</h2>
                        :
                            filtros && filtros.length > 0 &&(

                                <Table header={header}>{
                        
                                    filtros.map(el=>{
                                        return(
                                            <TableRow key={el._id}
                                            deleteOne={()=>handleDelete(el._id)}
                                            >
                                                <Col>{el.code}</Col>
                                                <Col>$ {el.precio}</Col>
                                                <Col>{el.cantidad}</Col>
                                                <Col>{el.categoria.name}</Col>
                                                <Col width='35%'>{el.aplicacion}</Col>
                                            </TableRow>
                                            )})
                                            }
                                </Table>
                            )
                    }

                </ResponsiveTable>


      </Container>
);
};

export default FiltrosTemplate;
