import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useLocation, useNavigate} from 'react-router-dom'
import defaultUser from '../../assets/img/defaultUser.png'
import { BorderRadius, Devices, ZIndex } from '../GlobalStyles'
import Dropdown, { DropdownItem } from '../molecules/buttons/Dropdown'
import Stack from '../atoms/divs/Stack'
import Heading from '../atoms/texts/Heading'


const NavbarContainer = styled.div`
    margin:0.5rem 0;
    position: sticky;
    top:0;
    display: flex;
    flex-direction:row;
    align-items:center;
    justify-content:space-between;
    wrap:nowrap;
    background-color:white;
    z-index:${ZIndex[50]};
`
const UserContainer = styled.div`
      display: flex;
      flex-direction:row;
      align-items:center;
      justify-content:space-around;
`;

const CustomImage = styled.img`
    display:none ;
    @media ${Devices.Laptops}{
        display: block;
        cursor: pointer;
        border-radius:${BorderRadius.md} ;
        width: 35px;
        height: 35px;
    }
`

const Navbar = ({user,logOut}) => {
    const Location = useLocation();
    const navigate = useNavigate();
    const [path, setPath] = useState('')

    useEffect(() => {
    function getPath(){
        if(Location.pathname === '/'){
            setPath('/inicio')
        }else{
            setPath('/'+Location.pathname.split('/')[1])
        }
    }
        getPath()
        return () => {
            
        }
    }, [Location])

    return (
        <NavbarContainer>
                    <Heading size='h2' color='bgPrimary' weight='bold'>
                        {path}
                    </Heading>
                    <UserContainer>
                        <Stack spacing={5}>
                            <Stack content='end' spacing={2}>
                                <CustomImage src={defaultUser} alt='Imagen del usuario' onClick={()=>navigate('/config')}/>
                                <Heading size='h6' color='fontDark'>{user?.name} </Heading>
                                <Heading size='h6' color='fontDark'>{user?.surname}</Heading>
                            </Stack>
                            <Dropdown top='60px' mobileTop='50px'>
                                <DropdownItem onClick={()=>navigate('/config')}>Configuracion</DropdownItem>
                                <DropdownItem onClick={logOut}>Cerrar Sesion</DropdownItem>
                            </Dropdown>
                        </Stack>
                    </UserContainer>
        </NavbarContainer>
    )
}

export default Navbar