import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import ActionButton from '../atoms/buttons/ActionButton';
import Stack from '../atoms/divs/Stack';
import Input from '../atoms/inputs/Input';
import Modal from '../molecules/divs/Modal';
import Message from '../molecules/texts/Message';
import PropTypes from 'prop-types'
import SelectSearch from '../atoms/inputs/SelectSearch';
import { BorderRadius } from '../GlobalStyles';

const Container = styled.form`
    width:100%;
`

const styles ={
    control:styles=>({
        ...styles,
        border:`1px solid #4B59F7`,
        outline:'none',
        borderRadius:BorderRadius.lg,
        width:'100%',
    })
}

const NewClient = ({
    form,
    onChange,
    setForm,
    open, 
    message,
    setOpen,
    onSubmit,
    options,
}) => {
    const [owner,setOwner]= useState({owner:''})

    function handleChange(e){
        setOwner({
            [e.name]:e.value
        })
        setForm({
            ...form,
            owner : e.value,
        })
    }

    useEffect(()=>{
    
    },[])

  return (
      <Modal title='Crear Nuevo' open={open} setOpen={setOpen}>
          <Container onSubmit={onSubmit}>
              {
                  message &&(
                      <Message color={message.ok ? 'success' : 'danger'}
                      width='3/5'
                      >
                          {message.msg}
                      </Message>
                  )
              }
              <Stack direction='column' margin='1rem 0'>
                <Input
                    type='text'
                    name='patent'
                    value={form.patent}
                    placeholder='Patente'
                    onChange={onChange}
                    required
                    color='primary'
                    width='3/4'
                />
                <SelectSearch
                    value={owner.owner}
                    onChange={handleChange}
                    styles={styles}
                    placeholder='Cliente'
                    options={options}
                />
                <Input
                    type='text'
                    name='mark'
                    value={form.mark}
                    placeholder='Marca'
                    onChange={onChange}
                    required
                    color='primary'
                    width='3/4'
                />
                <Input
                    type='text'
                    name='model'
                    value={form.model}
                    placeholder='Modelo'
                    onChange={onChange}
                    required
                    color='primary'
                    width='3/4'
                />
                <Input
                    type='number'
                    name='year'
                    value={form.year}
                    placeholder='anio'
                    onChange={onChange}
                    required
                    color='primary'
                    width='3/4'
                />
                <Input
                    type='number'
                    name='km'
                    value={form.km}
                    placeholder='Kms'
                    onChange={onChange}
                    required
                    color='primary'
                    width='3/4'
                />
              </Stack>
              <Stack margin='1rem 0'>
                  <ActionButton 
                  disabled={form? false : true}
                  >
                      Guardar
                  </ActionButton>
              </Stack>
          </Container>
      </Modal>
  );
};

NewClient.propTypes ={
    form:PropTypes.object,
    open:PropTypes.bool, 
    message:PropTypes.object,
    setOpen:PropTypes.func,
    onSubmit:PropTypes.func,
    onChange:PropTypes.func,
}

export default NewClient;
