import React from 'react';
import styled from 'styled-components';
import Wave from '../../assets/img/wave.svg'
import LoginImage from '../../assets/img/LoginImage.svg'
import { Devices } from '../GlobalStyles';
import LoginForm from '../organisms/LoginForm';
import Stack from '../atoms/divs/Stack';
import Heading from '../atoms/texts/Heading';
import Message from '../molecules/texts/Message';
import Links from '../atoms/texts/Links';

const LoginContainer = styled.div`
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color:  ${({ theme }) => theme.bg.secondary};
  background-image: ${`url(${Wave})`};
  background-size: cover;
`
const ContainerJunior = styled.div`
  padding: 1rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  border-radius: 1rem;
  background-color: white;
  box-shadow: 3px 1px 10px ${({ theme }) => theme.bg.secondary};
  height: max-content;
  @media ${Devices.Tablets} {
    grid-template-columns: repeat(2, 1fr);
    margin: 0.5rem 4rem;
    padding: 2rem;
  }
`
const LoginImg = styled.img`
  display: none;
  @media ${Devices.Tablets} {
    display: block;
    width: 100%;
    height: 100%;
  }
`
const FormContainer = styled.div`
  @media ${Devices.Tablets} {
    border-left: 2px solid ${({ theme }) => theme.bg.secondary};
    padding: 0 1rem;
    margin-left: 1rem;
  }
`
const BorderLine = styled.span`
  width: 25%;
  height: 5px;
  background: ${({ theme }) => theme.colors.secondary};
  @media ${Devices.Tablets} {
    width: 15%;
  }
`;

const LoginTemplate = ({onSubmit,onChange,email,password,error}) => {
  return(
      <>
        <LoginContainer>
            <ContainerJunior>
                <LoginImg src={LoginImage} alt='Imagen de Incio' />
                <FormContainer>
                  <Stack direction='column' spacing={1} content='start' items='start' margin='1rem 0'>
                        <BorderLine/>
                        <Heading color='bgPrimary' size='h5' weight='bold'>Portal de Ingreso</Heading>
                    </Stack>
                        {error ?
                        <Message color='danger' light={true}>{error.msg}</Message>
                        :
                        ''
                        }
                    <Stack direction='column' spacing={8} wrap='true'>
                        <LoginForm 
                        onSubmit={onSubmit}
                        onChange={onChange}
                        email={email}
                        password={password}
                        error={error}
                        />
                        <Links to='/'>Recuperar Contrasenia</Links>
                    </Stack>
                </FormContainer>
            </ContainerJunior>
        </LoginContainer>
      </>
  );
};

export default LoginTemplate;
