import React from "react";
import styled from "styled-components";
import { BorderRadius, widthFunction } from "../../GlobalStyles";
import { colorsFunction } from "../../Theme/Theme";

const Textarea = styled.textarea`
  outline: none;
  background: transparent;
  padding: 0.5rem 1rem;
  border-radius: ${BorderRadius.lg};
  border: ${({ theme, color }) =>
    color ? `1.5px solid ${colorsFunction(color, theme)}` : `none`};
  width: ${({ width }) => widthFunction(width)};
`;

const TextArea = ({
  id,
  type,
  name,
  value,
  placeholder = "Texto de ejemplo",
  onChange,
  required,
  color,
  width = "3/5",
  pattern,
  title,
  onFocus,
  ref,
  cols,
  rows
}) => {
  return (
    <Textarea
      id={id}
      name={name}
      value={value}
      type={type}
      color={color}
      placeholder={placeholder}
      required={required}
      onChange={onChange}
      width={width}
      pattern={pattern}
      title={title}
      onFocus={onFocus}
      ref={ref}
      cols={cols}
      rows={rows}
    />
  );
};

export default TextArea;
