import React from 'react';
import styled from 'styled-components';
import ActionButton from '../atoms/buttons/ActionButton';
import Stack from '../atoms/divs/Stack';
import Input from '../atoms/inputs/Input';
import Modal from '../molecules/divs/Modal';
import Message from '../molecules/texts/Message';
import PropTypes from 'prop-types'

const Container = styled.form`
    width:100%;
`

const NewClient = ({
    form,
    open, 
    message,
    setOpen,
    onSubmit,
    onChange,
}) => {




  return (
      <Modal title='Crear Nuevo' open={open} setOpen={setOpen}>
          <Container onSubmit={onSubmit}>
              {
                  message &&(
                      <Message color={message.ok ? 'success' : 'danger'}
                      width='100%'
                      >
                          {message.msg}
                      </Message>
                  )
              }
              <Stack direction='column' margin='1rem'>
                <Input
                    type='text'
                    name='name'
                    value={form.name}
                    placeholder='Nombre'
                    onChange={onChange}
                    required
                    color='primary'
                    width='3/4'
                />
                <Input
                    type='text'
                    name='surname'
                    value={form.surname}
                    placeholder='Apellido'
                    onChange={onChange}
                    required
                    color='primary'
                    width='3/4'
                />
                <Input
                    type='tel'
                    name='phone'
                    value={form.phone}
                    placeholder='Telefono'
                    onChange={onChange}
                    required
                    color='primary'
                    width='3/4'
                    pattern='[0-9]{7,12}'
                    title='ej 3876254380'
                />
                <Input
                    type='email'
                    name='email'
                    value={form.email}
                    placeholder='Email'
                    onChange={onChange}
                    color='primary'
                    width='3/4'
                />
              </Stack>
              <Stack margin='1rem 0'>
                  <ActionButton 
                  disabled={form&& form.name.length > 3 && form.surname.length > 3 && form.phone.length > 3 && form.email.length > 3? false : true}
                  >
                      Guardar
                  </ActionButton>
              </Stack>
          </Container>
      </Modal>
  );
};

NewClient.propTypes ={
    form:PropTypes.object,
    open:PropTypes.bool, 
    message:PropTypes.object,
    setOpen:PropTypes.func,
    onSubmit:PropTypes.func,
    onChange:PropTypes.func,
}

export default NewClient;
