import React from 'react';
import styled from 'styled-components';
import ActionButton from '../atoms/buttons/ActionButton';
import Button from '../atoms/buttons/Button';
import Stack from '../atoms/divs/Stack';
import {widthFunction } from '../GlobalStyles';

const Container = styled.table`
    width: ${({width})=> widthFunction(width)};
    text-align:center;
    border-collapse:collapse;
    overflow:hidden;
    `
const Head = styled.thead`
    border-bottom:1px solid #ddd;
    padding: 0.25rem;
`
const HeadItem =styled.th`
    color: ${({theme})=> theme.bg.primary};
`
const Row = styled.tr`
    &:hover {
        background-color:${({theme})=> theme.fontColor.primary};
        }
`
export const Col = styled.td`
    width:${({width})=> width ? width : 'max-content'};
    padding: 0.5rem;

`

const Table = ({children,width='4/5',header=['Nombre','Vehiculos','Telefono','Email','']}) => {
  return (
      <Container width={width}>
          <Head>
              <tr>
              {
                  header &&(
                    header.map(el=><HeadItem key={el}>{el}</HeadItem>)
                  )
              }
            </tr>
          </Head>
          <tbody>
              {children}
          </tbody>
      </Container>
  );
};

export default Table;

export const TableRow= ({children,seeMore,deleteOne})=>{
    return(
        <Row>
            {
                children
            }
                 <Col>
                        <Stack>
                            <Button onClick={seeMore}>Ver</Button>
                            <ActionButton onClick={deleteOne} type='danger'>Borrar</ActionButton>
                        </Stack>
                 </Col>
             </Row>
    );
}
