import React from 'react';
import styled from 'styled-components'
import PropTypes from 'prop-types'
import { FontWeight } from '../../GlobalStyles';
import { colorsFunction } from '../../Theme/Theme';

const Text = styled.span`
    margin: 0;
    padding:0;
    text-decoration:${({underline})=> underline ? 'underline': 'none' };
    font-size:${({size})=>{
        switch(size){
            case 'h1':
                return size='3rem'
            case 'h2':
                return size='2.25rem'
            case 'h3':
                return size='2rem'
            case 'h4':
                return size='1.75rem'
            case 'h5':
                return size='1.5rem'
            case 'h6':
                return size='1.25rem'
            default:
                return size='none'
        }
    }} ;
    font-weight:${({weight})=>{
      switch(weight){
        case 'normal':
          return weight = FontWeight.normal;
        case 'semibold':
          return weight = FontWeight.semibold;
        case 'bold':
          return weight = FontWeight.bold;
        case 'extrabold':
          return weight = FontWeight.extrabold;
        default :
        return weight = '';
      }
    }};
    color: ${({color,theme})=> colorsFunction(color,theme)};
`

const Heading = ({children, size='h4',color='primary' , weight='normal' ,underline}) => {
  return <Text size={size} color={color} weight={weight} underline={underline}>{children}</Text>;
};

Heading.propTypes={
    weight:PropTypes.oneOf(['normal','semibold','bold','extrabold']),
    size:PropTypes.oneOf(['h1','h2','h3','h4','h5','h6']),
    color:PropTypes.oneOf(['primary','secondary','bgPrimary','bgSecondary','fontLight','fontDark','success','warning','danger']),
    unerline:PropTypes.bool
}

export default Heading;
