import React from "react";
import styled from "styled-components";
import Tooltip from "../../atoms/icons/Tooltip";
import PropTypes from "prop-types";
import Input from "../../atoms/inputs/Input";
import { BorderRadius, widthFunction } from "../../GlobalStyles";
import { colorsFunction } from "../../Theme/Theme";

const Container = styled.div`
  width: ${({ width }) => widthFunction(width)};
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content:space-between;
  border: ${({ color, theme }) => `2px solid ${colorsFunction(color, theme)}`};
  padding-right: 0.5rem;
  border-radius: ${BorderRadius.lg};
`

const InpuTooltip = ({
  children,
  text='Ejemplo',
  size,
  color,
  type,
  name,
  value,
  placeholder="Texto de ejemplo",
  onChange,
  required,
  width='100%',
  onFocus,
}) => {
  return (
    <Container color={color} width={width} >
      <Input
        id={name}
        type={type}
        name={name}
        value={value}
        placeholder={placeholder}
        onChange={onChange}
        required={required}
        width="100%"
        onFocus={onFocus}
      />
      <label htmlFor={name}>
        <Tooltip tooltip text={text} size={size} color={color} top="-30%">
          {children}
        </Tooltip>
      </label>
    </Container>
  );
}
InpuTooltip.propTypes = {
  width:PropTypes.oneOf(['1/4','1/5','2/4','2/5','3/4','3/5','4/5','100%']),
  type: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  value: PropTypes.string.isRequired,
  placeholder: PropTypes.string,
  size: PropTypes.oneOf(["sm", "md", "lg", "xl"]),
  color: PropTypes.oneOf(['primary','secondary','bgPrimary','bgSecondary','fontLight','fontDark','success','warning','danger']),
  required:PropTypes.bool,
  onChange:PropTypes.func,
};
export default InpuTooltip;
