import React from 'react';
import { NavLink } from 'react-router-dom';
import styled from 'styled-components';
import { BorderRadius, Devices, ZIndex } from '../GlobalStyles';

const Container =styled.div`
    position:sticky ;
    top:44px;
    background: white;
    display: flex;
    flex-direction:row;
    justify-content:center;
    padding: 0.5rem 0 ;
    gap:1rem;
    z-index:${ZIndex[40]};
    @media ${Devices.Tablets}{
        justify-content:start;
    }
`

const InternBar = ({items}) => {

const Active ={
    background:'#4B59F7',
    borderRadius:BorderRadius.md,
    padding:'0.25rem 0.5rem',
    color:'#F4F7FB',
    fontWeight:'bold',
    textDecoration:'none',
    boxShadow:`1px 1px 5px #A9B3C1`
}
const Disabled ={
    background:'#A9B3C1',
    borderRadius:BorderRadius.md,
    padding:'0.25rem 0.5rem',
    color:'#151A31',
    fontWeight:'bold',
    textDecoration:'none',
    boxShadow:`1px 1px 5px #A9B3C1`
}

 return (
     items && items.length>0 ?
     <Container>{
            items.map(el=>{
                return(
                <NavLink key={el.name} to={el.page}
                style={({isActive})=> isActive ? Active : Disabled}>
                        {el.name}
                </NavLink>
                )})
            }
    </Container>
     :
     ''
    ) 
};

export default InternBar;
