import React, { useEffect, useState } from 'react';
import ClientesTemplate from '../../components/templates/clients/ClientesTemplate';
import AxiosInstance from '../../utils/AxiosInstance';

const initialForm ={
    name:'',
    surname:'',
    phone:'',
    email:'',
}


const Clientes = () => {
    const [form, setForm] = useState(initialForm);
    const [search, setSearch] = useState({search:''})
    const [isLoading, setIsLoading] = useState(true);
    const [clients, setClients] = useState([]);
    const [message, setMessage] = useState(null)
    const [clientDeleted, setClientDeleted] = useState(null)
    const [clientCreated, setClientCreated] = useState(null)


// Effcts
    useEffect(()=>{
        async function getClients(){
            let {data} =await  AxiosInstance.get('clients')
            if(data){
                setIsLoading(false)
                setClients(data.response.clients)
            }
        }
        async function searchClients(){
            if(search && search.search.length > 2){
                setIsLoading(true)
                let string = search.search.split(' ');
                let name = string[0];
                let surname = string[1];
                let {data} = await AxiosInstance.get(`clients/${name}/${surname}`)
                if( data && data.response.clients.length > 0){
                    setIsLoading(false)
                    setClients(data.response.clients)
                }
            }
        }

        if(search && search.search.length > 2){
            searchClients();
        }else{
            getClients();
        }
    },[search,clientDeleted,clientCreated])



// Funciones
    function handleForm(e){
        setForm({
            ...form,
            [e.target.name]:e.target.value,
        })
    }

    function handleSearch(e){
        setSearch({
            [e.target.name]:e.target.value,
        })
    }

    async function handleDelete(clientId,clientName){
        alert(`Al eliminar el Cliente ${clientName} tambien eliminaras todos los datos realacionados, incluyendo los datos de sus vehiculos`)
        let res = window.confirm(`Quieres eliminar el cliente ${clientName} ?`)
        if(res){
            let {data} = await AxiosInstance.delete(`client/${clientId}`)
            if(data){
                setMessage(data)
                setClientDeleted(data)
            }
            setInterval(()=>{
                setMessage(null)
            },5000)
                
        }
    }
    async function handleNew(e){
        e.preventDefault()
        let string = new URLSearchParams();
        string.append('name',`${form.name}`)
        string.append('surname',`${form.surname}`)
        string.append('phone',`${form.phone}`)
        string.append('email',`${form.email}`)

        let {data} = await AxiosInstance.post(`client`,string);
        setClientCreated(data)
        setMessage(data)
        setInterval(()=>{
            setMessage(null)
        },5000)
        setForm(initialForm);
    }


  return (
      <ClientesTemplate
            form={form}
            handleForm={handleForm}
            search={search.search}
            handleSearch={handleSearch}
            clients={clients}
            message={message}
            handleDelete={handleDelete}
            handleNew={handleNew}
            clientCreated={message}
            isLoading={isLoading}
      />
  );
};

export default Clientes;
