import React, { useState } from 'react';
import styled from 'styled-components';
import ActionButton from '../atoms/buttons/ActionButton';
import Stack from '../atoms/divs/Stack';
import Input from '../atoms/inputs/Input';
import SelectSearch from '../atoms/inputs/SelectSearch';
import TextArea from '../atoms/inputs/TextArea';
import { BorderRadius, Devices } from '../GlobalStyles';
import Modal from '../molecules/divs/Modal';
import Message from '../molecules/texts/Message';

const Container = styled.form`
    width: 100%;
    height:200px;
    @media ${Devices.Tablets}{
        height:300px;
    }
`
const styles ={
    control:styles=>({
        ...styles,
        border:`1px solid #4B59F7`,
        outline:'none',
        borderRadius:BorderRadius.lg,
        width:'100%',
    })
}


const NewProduct = ({
    open,
    setOpen,
    onSubmit,
    message,
    form,
    setForm,
    onChange,
    options,
}) => {

    const [categoria,setCategoria]= useState({categoria:''})

    function handleChange(e){
        setCategoria({
            [e.name]:e.value
        })
        setForm({
            ...form,
            categoria : e.value,
        })
    }



  return (
      <Modal setOpen={setOpen} open={open} title='Crear Nuevo Producto'>
          <Container onSubmit={onSubmit}>
              {
                  message &&(
                      <Message color={message.ok ? 'success' : 'danger'} light width='3/4'>
                          {message.msg}
                      </Message>
                  )
              }
              <Stack direction='column' spacing={2}>

                    <Stack>
                        <Input
                            type='text'
                            name='code'
                            value={form?.code}
                            onChange={onChange}
                            width='3/5'
                            color='primary'
                            placeholder='Codigo'
                            required
                        />
                        <Input
                            type='number'
                            name='precio'
                            value={form?.precio}
                            onChange={onChange}
                            width='3/5'
                            color='primary'
                            placeholder='Precio'
                            required
                        />
                    </Stack>
                    <Stack>
                        <Input
                            type='number'
                            name='cantidad'
                            value={form?.cantidad}
                            onChange={onChange}
                            width='3/5'
                            color='primary'
                            placeholder='Cantidad'
                            required
                        />
                        <SelectSearch
                        options={options}
                        styles={styles}
                        placeholder='Categoria'
                        value={categoria.categoria}
                        onChange={handleChange}
                        />
                    </Stack>

                <TextArea 
                name='aplicacion'
                value={form?.aplicacion}
                onChange={onChange}
                placeholder='Aplicacion'
                color='primary' 
                cols='5' 
                rows='10'/>
              </Stack>
              <Stack margin='1rem 0'>
                    <ActionButton type='success'>
                            Guardar
                    </ActionButton>
              </Stack>
          </Container>
      </Modal>
  );
};

export default NewProduct;
