import React from 'react';
import styled from 'styled-components';
import { BorderRadius, Disabled, FontWeight } from '../../GlobalStyles';
import PropTypes from 'prop-types'

const ButtonStyled = styled.button`
  position:relative;
  padding: 0.5rem 1rem ;
  border-radius:${BorderRadius.md};
  border: none;
  cursor:pointer;
  font-weight:${FontWeight.normal};
  letter-spacing:1px;
  box-shadow:${({theme})=>theme.boxShadow.md};
  color:${({theme})=>theme.fontColor.primary};
  ${({disabled})=> disabled ? Disabled : ''}
  background-color :${({type,theme})=>{
      switch(type){
          case 'warning':
              return type = theme.action.warning;
          case 'danger':
              return type = theme.action.danger;
          case 'success':
              return type = theme.action.success;
        default :
        return type = '';
      }
  }};
   &:hover{
      &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(0, 0, 0, 0.15);
      border-radius: 8px;
      border: none;
      }
    }
`

const ActionButton = ({children, type='success', onClick,disabled}) => {
  return <ButtonStyled type={type} onClick={onClick} disabled={disabled}>{children}</ButtonStyled>;
};

ActionButton.propTypes = {
    type:PropTypes.oneOf(['success','warning','danger']),
}
export default ActionButton;
