import { useState } from "react";
import { ThemeProvider} from "styled-components";


export const light = {
    colors:{
        primary:'#4B59F7',
        secondary:'#0467FB',
    },
    bg:{
        primary:'#151A31',
        secondary:'#A9B3C1',
    },
    action:{
        success:'#22bb33',
        warning:'#f0ad4e',
        danger:'#bb2124',
    },
    fontColor:{
        primary:'#F4F7FB',
        secondary:'#E0E5E8',
        black:'#010400',
        blackLight:'#1F201E',
    },
    boxShadow:{
        default: '0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1)',
        sm: '0 1px 2px 0 rgb(0 0 0 / 0.05)',
        md: '0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1)',
        lg: '0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1)',
        xl: '0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1)',
        '2xl': '0 25px 50px -12px rgb(0 0 0 / 0.25)',
        inner: 'inset 0 2px 4px 0 rgb(0 0 0 / 0.05)',
    }
}
export const dark = {
    colors:{
        primary:'#ffffff',
        secondary:'black',
    },
    bg:{
        primary:'#0467FB',
        secondary:'#E0E5E8',
    },
    action:{
        success:'#22bb33',
        warning:'#f0ad4e',
        danger:'#bb2124',
    },
    fontColor:{
      primary:'#010400',
      secondary:'#E0E5E8',
      black:'#F4F7FB',
      blackLight:'#F4F7FB',
    },
    boxShadow:{
        default: '0 1px 3px 0 rgb(0 0 0 / 0.1), 0 1px 2px -1px rgb(0 0 0 / 0.1)',
        sm: '0 1px 2px 0 rgb(0 0 0 / 0.05)',
        md: '0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1)',
        lg: '0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1)',
        xl: '0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1)',
        '2xl': '0 25px 50px -12px rgb(0 0 0 / 0.25)',
        inner: 'inset 0 2px 4px 0 rgb(0 0 0 / 0.05)',
    }
}

export function colorsFunction(color,theme){
    switch(color){
        // Colors
      case 'primary':
        return color=theme.colors.primary
      case 'secondary':
        return color=theme.colors.secondary
      case 'success':
        //   Action Colors
        return color=theme.action.success
      case 'warning':
        return color=theme.action.warning
      case 'danger':
        return color=theme.action.danger
      case 'bgPrimary':
        //   Bg Colors
        return color=theme.bg.primary
      case 'bgSecondary':
        return color=theme.bg.secondary
        //   Font Colors
      case 'fontLight':
        return color=theme.fontColor.primary
      case 'fontDark':
        return color=theme.fontColor.black
        default :
        return color='none'
    }
  }

const Theme = ({children})=>{
    const [theme,setTheme] = useState(light)
    return <ThemeProvider theme={theme}>{children}</ThemeProvider>
}

export default Theme