import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import styled from 'styled-components';
import { Devices } from '../../GlobalStyles';
import Message from '../../molecules/texts/Message';
import NewClient from '../../organisms/NewClient';
import OutletSearchBar from '../../organisms/OutletSearchBar';
import Table, { Col } from '../../organisms/Table';
import {TableRow} from '../../organisms/Table';

const Container = styled.div`
    margin: 2rem 0;
`
const ResponsiveTable = styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    margin:1.5rem 0;
    /* max-width:500px; */
    overflow:hidden;
    overflow-x:scroll ;
    overflow-y:scroll ;
    @media ${Devices.Tablets}{
        overflow-x:auto ;
        overflow-y:auto ;
    }
`
const ClientesTemplate = ({
    form,
    handleForm,
    search, 
    handleSearch,
    clients,
    handleDelete,
    message,
    handleNew,
    clientCreated,
    isLoading,
}) => {
    const [addOne, setAddOne] = useState(false)
    const navigate = useNavigate();

  return (
        <Container>
            <OutletSearchBar
                placeholder='Ingrese el nombre del cliente'
                search={search} onChange={handleSearch}
                addOne={addOne} setAddOne={setAddOne}
            />
            <NewClient 
            open={addOne} 
            setOpen={setAddOne} 
            form={form} 
            onChange={handleForm} 
            onSubmit={handleNew} 
            message={clientCreated}
            />

            {
                message &&(
                        <Message color='danger' light={true} width='4/5'>
                            {message.msg}
                        </Message>
                )
            }
            <ResponsiveTable >


                    {
                        isLoading ?
                        <h2>Cargando</h2>
                        :
                        clients && clients.length > 0 &&(
                        <Table>{
                        
                        clients.map(el=>{
                            return(
                                <TableRow key={el._id}
                                seeMore={()=> navigate(`${el._id}`)}
                                deleteOne={()=>handleDelete(el._id,`${el.name} ${el.surname}`)}
                                >
                                    <Col>{el.name} {el.surname}</Col>
                                    <Col>{el.vehicles.length}</Col>
                                    <Col>{el.phone}</Col>
                                    <Col>{el.email}</Col>
                                </TableRow>
                                )})
                                }
                        </Table>
                        )
                    }                
                </ResponsiveTable>
        </Container>
  );
};

export default ClientesTemplate;
