import React from 'react';
import { IoClose } from 'react-icons/io5';
import styled from 'styled-components';
import ActionButton from '../../atoms/buttons/ActionButton';
import Stack from '../../atoms/divs/Stack';
import Heading from '../../atoms/texts/Heading';
import { ZIndex } from '../../GlobalStyles';
import PropTypes from 'prop-types'

const ModalContainer = styled.div`
    width: 100%;
    height:100vh;
    position:fixed;
    top:0;
    left:0;
    background:rgba(0,0,0,.5);
    display: flex;
    align-items:center;
    justify-content:center; 
    z-index:${ZIndex[50]};
    `
const BodyContainer = styled.div`
    width: 500px;
    background: white;
    border-radius:14px;
    padding:0.5rem 1rem;

    `
const ModalHeader = styled.div`
    border-bottom:1px solid ${({theme})=>theme.bg.secondary};
`
const ModalContent = styled.div`
    padding: 0.5rem 0;
    overflow:auto;
`
const ModalFooter = styled.div`
    padding: 0.5rem 0;
    border-top:1px solid ${({theme})=>theme.bg.secondary};
`
const CloseButton = styled.button`
    background: transparent;
    border:none;
    font-size:2rem ;
    cursor: pointer;
`

const Modal = ({children,title='Modal',open, setOpen}) => {
  return (
      open ?
      <ModalContainer>
          <BodyContainer>
                {/* Header */}
                <ModalHeader>
                    <Stack content='space-between'>
                        <Heading size='h5' color='fontDark' weight='bold'>
                            {title}
                        </Heading>
                        <CloseButton onClick={()=>setOpen(!open)}>
                            <IoClose/>
                        </CloseButton>
                    </Stack>
                </ModalHeader>
                {/* Content */}
                <ModalContent>
                    <Stack>
                        {children}
                    </Stack>
                </ModalContent>
                {/* Footer */}
                <ModalFooter>
                    <Stack content='start'>
                        <ActionButton type='danger' onClick={()=>setOpen(!open)}>
                            Cerrar
                        </ActionButton>
                    </Stack>
                </ModalFooter>
          </BodyContainer>
      </ModalContainer>
      :
      ''
  );
};

Modal.propTypes ={
    title:PropTypes.string,
    open:PropTypes.bool,
    setOpen:PropTypes.func,
}
export default Modal;
