import React from 'react';
import { IoLockClosedOutline, IoPersonOutline } from 'react-icons/io5';
import styled from 'styled-components';
import Button from '../atoms/buttons/Button';
import Stack from '../atoms/divs/Stack';
import InpuTooltip from '../molecules/inputs/InpuTooltip';
import PropTypes from 'prop-types'

const LoginContainer = styled.form`
    width: 100%;
`

const LoginForm = ({onSubmit,email,password,onChange,error}) => {
  return (
      <LoginContainer onSubmit={onSubmit}>
          <Stack direction='column' spacing={4} wrap='true'>
            <InpuTooltip text='Email' value={email} name='email' type='email' placeholder='Email' color={error ? 'danger' : 'secondary'} onChange={onChange} required width='3/4' >
                <IoPersonOutline/>
                </InpuTooltip>
            <InpuTooltip text='Password' value={password} name='password' type='password' placeholder='Password' color={error ? 'danger' : 'secondary'} onChange={onChange} required width='3/4' >
                <IoLockClosedOutline/>
                </InpuTooltip>
                <Button width='2/5' disabled={
                    email && email.length > 3 && password && password.length >3 ? false : true
                }>
                    Ingresar
                </Button>
          </Stack>
      </LoginContainer>
  );
};

LoginForm.propTypes ={
    onSubmit:PropTypes.func,
    onChange:PropTypes.func,
    email:PropTypes.string,
    password:PropTypes.string,
    error:PropTypes.object,

}

export default LoginForm;
