import React from 'react';
import styled from 'styled-components';
import { colorsFunction } from '../../Theme/Theme';
import PropTypes from 'prop-types'
import Paragraph from '../../atoms/texts/Paragraph';
import { BorderRadius, widthFunction } from '../../GlobalStyles';

const Container = styled.div`
    margin:0.5rem 0 ;
    width:${({width})=> widthFunction(width)} ;
    padding:0 1rem ;
    border-radius:${BorderRadius.sm};
    background-color:${({color,theme})=> colorsFunction(color,theme)} ;
    text-align:center;
    wrap:wrap;
`

const Message = ({children,color='bgSecondary',light,width='100%'}) => {
  return (
      <Container color={color} width={width}>
            <Paragraph color={light ? 'fontLight' : 'fontDark'} weight='bold'>
                        {children}
            </Paragraph>
      </Container>
  );
};

Message.propTypes = {
    color:PropTypes.oneOf(['primary','secondary','bgPrimary','bgSecondary','fontLight','fontDark','success','warning','danger']),
    light:PropTypes.bool,
    width:PropTypes.oneOf(['1/4','1/5','2/4','2/5','3/4','3/5','4/5','100%']),

}
export default Message;
