import React, { useEffect, useState } from 'react';
import FiltrosTemplate from '../../components/templates/products/FiltrosTemplate';
import AxiosInstance from '../../utils/AxiosInstance';


const initialForm= {
  code:'',
  precio:'',
  categoria:'',
  aplicacion:'',
  cantidad:'',
}

const Filtros = () => {
    const [form, setForm] = useState(initialForm)
    const [search, setSearch] = useState({search:''})
    const [isLoading, setIsLoading] =useState(true)
    const [filtros, setFiltros] = useState([])
    const [options, setOptions] = useState([])
    const [message, setMessage] = useState(null)
    const [deleted, setDeleted] = useState(null)
    const [created, setCreated] = useState(null)


// Efectos
  useEffect(()=>{
      async function getFiltros(){
        let {data} = await AxiosInstance.get('products')
        if(data && data.response.length > 0){
          setIsLoading(false);
          setFiltros(data.response)
        }
      }

      async function searchFiltros(){
        let {data} = await AxiosInstance.get(`products/${search.search}`)
        if(data && data.response.length > 0){
          setIsLoading(false);
          setFiltros(data.response)
        }
      }

      if(search && search.search.length > 2){
        searchFiltros()
      }else{
        getFiltros()
      }
  },[search,deleted,created])

  useEffect(()=>{
    async function getCategorias(){
      let {data} = await AxiosInstance.get('category');
      if(data && data.response.categories.length > 0){
        let categorias = data.response.categories;
                categorias = categorias.map(el=>{
                    return({
                        value:el._id,
                        label:`${el.name}`,
                    })
                })
                setOptions(categorias)
      }
    }
    getCategorias()
  },[])



// Funciones
  function handleForm(e){
    setForm({
      ...form,
      [e.target.name]:e.target.value,
    })
  }
  function handleSearch(e){
    setSearch({
      [e.target.name]:e.target.value,
    })
  }
  async function handleDelete(productId){
    let {data} = await AxiosInstance.delete(`product/${productId}`);
    if(data){
      setMessage(data)
      setDeleted(data)
      setInterval(()=>{
        setMessage(null)
      },5000)
    }
  }

  async function handleNew(e){
    e.preventDefault()
    let string = new URLSearchParams();
    string.append(`code`,`${form.code}`)
    string.append(`precio`,`${form.precio}`)
    string.append(`cantidad`,`${form.cantidad}`)
    string.append(`categoria`,`${form.categoria}`)
    string.append(`aplicacion`,`${form.aplicacion}`)
    try{
      let {data} = await AxiosInstance.post('product',string)
      if(data){
        setMessage(data)
        setCreated(data)
        setInterval(()=>{
          setMessage(null)
        },7000)
      }
      setForm(initialForm)
      
    }catch(err){
      setMessage(err?.response?.data)
      setInterval(()=>{
        setMessage(null)
      },7000)
    }
  }

  return (
      <FiltrosTemplate
        form={form}
        handleForm={handleForm}
        setForm={setForm}
        search={search.search}
        handleSearch={handleSearch}
        isLoading={isLoading}
        options={options}
        filtros={filtros}
        message={message}
        handleDelete={handleDelete}
        handleNew={handleNew}
      />
  );
};

export default Filtros;
