import { useEffect, useRef, useState } from "react"
import { IoClose, IoReorderFour } from "react-icons/io5"
import styled from "styled-components"
import Stack from "../../atoms/divs/Stack"
import IconContainer from "../../atoms/icons/IconContainer"
import { BorderRadius, Devices, ZIndex } from "../../GlobalStyles"
import PropTypes from 'prop-types'



const DropdownContainer = styled.div`
        display: flex;
        flex-direction:row ;
        align-items:center;
        justify-content:space-around;
        `
    const DropdownButton = styled.button`
        border: none;
        background-color:transparent;
        border-radius:8px;
        padding: 0.5rem;
    `
    const ItemContainer =styled.div`
        position:absolute;
        top:${({mobileTop})=> mobileTop };
        left:${({mobileLeft})=> mobileLeft};
        width:125px;
        transform:translateX(-45%);
        border:1px solid ${({theme})=> theme.bg.primary} ;
        border-radius:${BorderRadius.md};
        overflow:hidden;
        z-index:${ZIndex[50]};
        @media ${Devices.Tablets}{
            top:${({top})=> top };
            left:${({left})=> left};
        }
    `
const Dropdown = ({children,top='10%',left,size='md',mobileTop='10%',mobileLeft}) => {
    const [open, setOpen] = useState(false)
    let dropdownRef = useRef();

    useEffect(() => { 
        let handler= (e)=> {
            if(!dropdownRef.current.contains(e.target)){
                setOpen(false);
            }
        }
        document.addEventListener('mousedown',handler);
        return () => {
            document.removeEventListener('mousedown',handler);
        }
    }, [open])

    return (
        <DropdownContainer ref={dropdownRef} >
            <DropdownButton onClick={()=> setOpen(!open)} open={open}>
                {
                    open ?
                <IconContainer size={size} color="bgPrimary">
                    <IoClose/>
                </IconContainer>
                :
                <IconContainer size={size} color="bgPrimary">
                    <IoReorderFour/>
                </IconContainer>
                }
            </DropdownButton>
                    {open &&(
                    <ItemContainer open={open} top={top} left={left} mobileTop={mobileTop} mobileLeft={mobileLeft}>
                        <Stack direction="column" spacing={0}>
                        {children}
                        </Stack>
                    </ItemContainer>
                    )}
        </DropdownContainer>
    )
}

Dropdown.propTypes={
    top:PropTypes.string,
    mobileTop:PropTypes.string,
    mobileLeft:PropTypes.string,
    left:PropTypes.string,
    size:PropTypes.oneOf(['sm','md','lg','xl']),

}
export default Dropdown

const Item = styled.button`
  position: relative;
  width: 100%;
  background: ${({ theme }) => theme.fontColor.secondary};
  color: ${({ theme }) => theme.bg.primary};
  font-size: 0.75rem;
  font-weight: bold;
  border: none;
  transition: background border 0.3s ease-in;
  cursor: pointer;
  padding: 0.75rem;
  &:hover {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(0, 0, 0, .25);
      border: none;
    }
  }
`;

export const DropdownItem = ({ children, onClick }) => {
  return <Item onClick={onClick}>{children}</Item>;
};
DropdownItem.propTypes={
  onClick:PropTypes.func,
}
