import React from 'react';
import { Outlet } from 'react-router-dom';
import InternBar from '../../organisms/InternBar';

const ProductsTemplate = () => {
    const pages =[

        {
            name:'Filtros',
            page:'/productos'
        },
        {
            name:'Servicios',
            page:'/productos/servicios'
        },
        {
            name:'Categorias',
            page:'/productos/categorias'
        },
    ]
  return (
      <>
        <InternBar items={pages}/>
        <Outlet/>
      </>
  );
};

export default ProductsTemplate;
