import React from 'react';
import styled from 'styled-components';
import { BorderRadius, BorderWidth, Disabled, FontWeight,widthFunction} from '../../GlobalStyles';
import PropTypes from 'prop-types'


const ButtonStyled = styled.button`
  padding: 0.5rem 1rem ;
  border-radius:${BorderRadius.md};
  border: none;
  cursor:pointer;
  font-weight:${FontWeight.semibold};
  letter-spacing:1px;
  box-shadow:${({theme})=>theme.boxShadow.md};
  width: ${({ width }) => widthFunction(width)};
  ${({disabled})=> disabled ? Disabled : ''}
  ${({type,theme})=>{
    if(type === 'primary'){
      return(
      `
    position: relative;
    background-color:${theme.colors.primary} ;
    color:${theme.fontColor.primary};
    transition: all 0.5s ease-in;
      &:hover{
      &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: rgba(0, 0, 0, 0.15);
      border-radius: 8px;
      border: none;
      }
    }
      `)
    }
    if(type === 'secondary'){
      return(`
      background-color:transparent;
      border-radius:${BorderRadius.lg};
      padding: 0.5rem 1rem ;
      border: ${BorderWidth[2]} solid ${theme.colors.secondary};
      color:${theme.colors.secondary} ;
      transition: all 0.25s ease-in;
      &:hover{
        background-color:${theme.colors.secondary} ;
        color:${theme.fontColor.primary} ;
      }
      `)
    }
  }}
}
`

const Button = ({children,type='primary', onClick, disabled , width='1/4'}) => {
  return <ButtonStyled type={type} onClick={onClick} disabled={disabled} width={width}>{children}</ButtonStyled>;
};

Button.propTypes ={
  width:PropTypes.oneOf(['xs','md','lg','1/4','1/5','2/4','2/5','3/4','3/5','4/5']),
  type:PropTypes.oneOf(['primary','secondary']),
  onClick:PropTypes.func,
}

export default Button;
