import React, { useState } from 'react';
import styled from 'styled-components';
import ActionButton from '../../atoms/buttons/ActionButton';
import Stack from '../../atoms/divs/Stack';
import Input from '../../atoms/inputs/Input';
import { Devices } from '../../GlobalStyles';
import Modal from '../../molecules/divs/Modal';
import Message from '../../molecules/texts/Message';
import OutletSearchBar from '../../organisms/OutletSearchBar';
import Table, { Col, TableRow } from '../../organisms/Table';

const Container = styled.div`
    margin: 2rem 0;

`
const ContainerForm = styled.form`
    width: 100%;
    margin: 2rem 0;

`
const ResponsiveTable = styled.div`
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:center;
    margin:1.5rem 0;
    overflow:hidden;
    overflow-x:scroll ;
    overflow-y:scroll ;
    @media ${Devices.Tablets}{
        overflow-x:auto ;
        overflow-y:auto ;
    }
`

const CategoriasTemplate = ({
    search,
    handleSearch,
    form,
    handleForm,
    isLoading,
    categorias,
    message,
    handleDelete,
    handleNew

}) => {
    const [addOne , setAddOne] = useState(false)
    const [newCat , setNewCat] = useState({name:''})

    function handleNewCat(e){
        setNewCat({
            [e.target.name]:e.target.value
        })
    }

  return (

    <Container>
            <OutletSearchBar
            placeholder='Ingrese el nombre de la categoria'
            search={search} onChange={handleSearch}
            addOne={addOne} setAddOne={setAddOne}
            />

            <Modal open={addOne} setOpen={setAddOne} title='Crear Categoria'>
                <ContainerForm onSubmit={handleNew}>

                    {
                        message &&(
                            <Message color={message?.ok ? 'succes' : 'danger'} light width='3/5'>
                                    {message.msg}
                            </Message>
                        )
                    }

                    <Stack direction='column' spacing={8}>

                        <Input 
                        type='text'
                        width='3/5' 
                        color='primary' 
                        placeholder='Nombre'
                        name='name'
                        value={newCat.name}
                        onChange={handleNewCat}
                        />
                        <ActionButton
                        disabled={newCat && newCat.name.length > 3 ? false : true}
                        >
                            Guardar
                        </ActionButton>
                    </Stack>
                </ContainerForm>
            </Modal>
            {
                message &&(
                    <Message>
                        {message.msg}
                    </Message>
                )

            }
            <ResponsiveTable>
                {
                    isLoading ?
                    <h2>Cargando</h2>
                    :
                    categorias && categorias.length > 0 &&(
                        <Table header={['Nombre','Productos',' ']}>
                                {
                                    categorias.map(el =>{
                                        return(
                                            <TableRow
                                            key={el._id}
                                            deleteOne={()=>handleDelete(el._id, el.name)}
                                            >
                                                <Col>{el.name}</Col>
                                                <Col>{el.products.length}</Col>
                                            </TableRow>
                                        )
                                    })
                                }
                        </Table>
                    )
                }
            </ResponsiveTable>

    </Container>


  );
};

export default CategoriasTemplate;
